<div class="w-full h-[100vh] flex flex-wrap items-center justify-center"><div class="drop-zone w-full md:w-[600px] h-[200px] md:h-[400px] flex flex-col items-center justify-center cursor-pointer border-[#009578] border-4 rounded-2xl bg-[#e0ffb5]" v-if="!tableData.length" ref="fileDiv" :class="[       borderStyle ? 'border-solid' : 'border-dashed',     ]" @click="choseFile" @drop="dropFile" @dragover="dragOver" @dragleave="setBorder" @dragend="setBorder"><span class="drop-zone__prompt">Drop file here or click to upload</span><input class="drop-zone__input hidden" ref="fileInput" type="file" name="myFile" @change="changeFile"></div><div class="w-full h-auto px-1" v-else><el-table :data="tableData" :height="isMobile ? 400 : 500" style="width: 100%"><el-table-column prop="A1" label="Date" fit="true"></el-table-column><el-table-column prop="B1" label="Name" fit="true"></el-table-column><el-table-column prop="C1" label="Address" fit="true"></el-table-column></el-table><button class="bg-green-500 text-white my-2 py-2 px-4 font-medium rounded-xl transition-all duration-300 hover:bg-green-400" @click="clear">清除</button></div></div>